import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import { BrowserRouter } from "react-router-dom";

const app = (
  <BrowserRouter basename="/Burger-Project/">
    <App />
  </BrowserRouter>
);

ReactDOM.render(app, document.getElementById("root"));

